<template>
    <div class="card">
        <div class="card-body address">
            <div class="address__text">
                {{address}}
            </div>
            <div class="address__buttons">
                <button @click="handleAccept()" type="button" class="btn btn-secondary" style="margin-right: 10px">Accept Ownership</button>
                <button @click="handleCancel()" type="button" class="btn btn-danger">Cancel</button>
            </div>
        </div>
    </div>
</template>

<script>
import { mapActions } from 'vuex'

export default {    
props: {
    address: String
},
methods: {
    ...mapActions(['SEND_DATA']),
    async handleCancel() {
        const data = {
                method: 'cancelTransferProfileOwnership',
                params: [this.address]
        }
        await this.SEND_DATA(data)
    },
    async handleAccept() {
        const data = {
                method: 'acceptProfileOwnership',
                params: [this.address]
        }
        await this.SEND_DATA(data)
    }
}
}
</script>

<style scoped>
.address {
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.card {
    margin: 10px;
}
.premodal {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: 1000;
}
.dialog {
    border: 2px solid rgba(0, 0, 0, 0.175);
    border-radius: 10px;
    padding: 20px;
    width: 900px;
    margin-top: 300px;
    background: white;
}

</style>
      