<template>
    <div class="card">
        <div class="card-body address">
            <div class="address__text">
                {{address}}
            </div>
            <div class="address__buttons">
                <button @click="openModal()" type="button" class="btn btn-secondary" style="margin-right: 10px">Transfer Ownership</button>
                <button @click="handleDelete()" type="button" class="btn btn-danger">Delete</button>
            </div>
        </div>
        <div v-if="isOpen" class="premodal">
            <div class="modal-dialog dialog">
                <div class="modal-content">
                  <div class="modal-header">
                    <h5 class="modal-title">Transfer Ownership</h5>
                    <button @click="closeModal()" type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Закрыть"></button>
                  </div>
                  <div class="modal-body">
                    <div class="mb-3 row">
                        <label for="staticEmail" class="col-sm-2 col-form-label">Profile Address:</label>
                        <div class="col-sm-10">
                          <input type="text" readonly class="form-control-plaintext" id="staticEmail" :value="address">
                        </div>
                      </div>
                      <div class="mb-3 row">
                        <label for="input1" class="col-sm-2 col-form-label">New Owner Address:</label>
                        <div class="col-sm-10">
                          <input v-model="newOwner" type="text" class="form-control" id="input1">
                        </div>
                    </div>
                  </div>
                  <button @click="handleTransfer()" type="button" class="btn btn-info">Transfer</button>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import { mapActions } from 'vuex'

export default {    
data() {
    return {
        newOwner: '',
        isOpen: false
    }
},
props: {
    address: String
},
methods: {
    ...mapActions(['SEND_DATA']),
    async handleDelete() {
        const data = {
                method: 'deleteProfile',
                params: [this.address]
        }
        await this.SEND_DATA(data)
        this.isOpen = false
    },
    async handleTransfer() {
        const data = {
                method: 'transferProfileOwnership',
                params: [this.newOwner, this.address]
        }
        await this.SEND_DATA(data)
        this.isOpen = false
    },
    openModal() {
        this.isOpen = true;
    },
    closeModal() {
        this.isOpen = false;
    }
}
}
</script>

<style scoped>
.address {
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.card {
    margin: 10px;
}
.premodal {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: 1000;
}
.dialog {
    border: 2px solid rgba(0, 0, 0, 0.175);
    border-radius: 10px;
    padding: 20px;
    width: 900px;
    margin-top: 300px;
    background: white;
}

</style>
      