import Vue from 'vue'
import Vuex from 'vuex'
import {
  connect,
  checkAddress,
  sendDataToContract,
  fetchContractData,
  getFirstAddress
} from '@/utils/web3.js';

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    loader: false,
    isConnected: false,
    message: '',
    profilesArray: [],
    transmittedProfiles: [],
    acceptedProfiles: []
  },
  getters: {
    IS_CONNECTED: state => state.isConnected,
    MESSAGE: state => state.message,
    LOADER: state => state.loader,
    PROFILES_ARRAY: state => state.profilesArray,
    TRANSMITTED_PROFILES: state => state.transmittedProfiles,
    ACCEPTED_PROFILES: state => state.acceptedProfiles,
  },
  mutations: {
    SET_CONNECT_STATUS: (state, status) => state.isConnected = status, 
    SET_LOADER_STATUS: (state, status) => state.loader = status, 
    SET_MESSAGE: (state, msg) => state.message = msg,
    SET_PROFILES_ARRAY: (state, array) => state.profilesArray = array,
    SET_TRANSMITTED_PROFILES: (state, array) => state.transmittedProfiles = array,
    SET_ACCEPTED_PROFILES: (state, array) => state.acceptedProfiles = array,
  },
  actions: {
    async CONNECT_METAMASK({commit}) {
      commit('SET_LOADER_STATUS', true);
      const status = await connect();
      if(status) {
        commit('SET_CONNECT_STATUS', true);
      }
      commit('SET_LOADER_STATUS', false);
    },
    async SEND_DATA({commit}, data) {
      commit('SET_LOADER_STATUS', true);
      commit('SET_MESSAGE', '');
      const addresses = await checkAddress();
      if(addresses) {
        const message = await sendDataToContract(data.method, data.params);
        if (message.hash) {
          commit('SET_MESSAGE', `https://goerli.etherscan.io/tx/${message.hash}`);
        } else if (message.error.message) {
          commit('SET_MESSAGE', message.error.message);
        }
      } else {
        commit('SET_CONNECT_STATUS', false);
      }
      commit('SET_LOADER_STATUS', false);
    },
    async GET_PROFILES_ARRAY({commit}) {
      commit('SET_PROFILES_ARRAY', []);
      const user = await getFirstAddress()
      const length = await fetchContractData('getLengthProfilesArray', [user]);
      const r = await fetchContractData('getSliceProfilesArray', [user, 0, length]);
      console.log(r);
      const array = r;
      commit('SET_PROFILES_ARRAY', array);
    },
    async GET_TRANSMITTED_PROFILES({commit}) {
      commit('SET_TRANSMITTED_PROFILES', []);
      const user = await getFirstAddress()
      const r = await fetchContractData('getTransmittedProfiles', [user]);
      const array = r;
      commit('SET_TRANSMITTED_PROFILES', array);
    },
    async GET_ACCEPTED_PROFILES({commit}) {
      commit('SET_ACCEPTED_PROFILES', []);
      const user = await getFirstAddress()
      const r = await fetchContractData('getAcceptedProfiles', [user]);
      const array = r;
      commit('SET_ACCEPTED_PROFILES', array);
    },
  },
  modules: {
  }
})