<template>
    <div class="card">
        <div class="card-body address">
            <div class="address__text">
                {{address}}
            </div>
            <div class="address__buttons">
                <button @click="handleTransfer()" type="button" class="btn btn-secondary">Cancel Transfer</button>
            </div>
        </div>
    </div>
</template>

<script>
import { mapActions } from 'vuex'

export default {    
props: {
    address: String
},
methods: {
    ...mapActions(['SEND_DATA']),
    async handleTransfer() {
        const data = {
                method: 'cancelTransferProfileOwnership',
                params: [this.address]
        }
        await this.SEND_DATA(data)
    }
}
}
</script>

<style scoped>
.address {
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.card {
    margin: 10px;
}
</style>
      