<template>
<div class="hello">
    <div class="home__container">
        <div class="input-group mb-3">
            <span class="input-group-text" id="inputGroup-sizing-default">Full Name</span>
            <input v-model="fullName" type="text" class="form-control" aria-describedby="inputGroup-sizing-default">
        </div>
        <button @click="handleCreateProfile" type="button" class="btn btn-primary">Create New Profile Contract</button>
    </div>
    
    <div class="home__title">
        My Profiles:
    </div>
    <div class="home__container">
        <profile-address :address="address" v-for="address, index in PROFILES_ARRAY" :key="index" />
    </div>
    <div class="home__title">
        Transmitted Profiles:
    </div>
    <div class="home__container">
        <transmitted-profile :address="address" v-for="address, index in TRANSMITTED_PROFILES" :key="index" />
    </div>
    <div class="home__title">
        AcceptedProfiles
    </div>
    <div class="home__container">
        <accepted-profile :address="address" v-for="address, index in ACCEPTED_PROFILES" :key="index" />
    </div>
</div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex'
import ProfileAddress from '@/components/ProfileAddress.vue'
import TransmittedProfile from '@/components/TransmittedProfile.vue'
import AcceptedProfile from '@/components/AcceptedProfile.vue'

export default {    
    name: 'HelloWorld',
    components: {
        ProfileAddress,
        TransmittedProfile,
        AcceptedProfile
    },
    created() {
        this.GET_PROFILES_ARRAY()
        this.GET_TRANSMITTED_PROFILES()
        this.GET_ACCEPTED_PROFILES()
    },
    data() {
        return {
            fullName: ''
        }
    },
    methods: {
        ...mapActions(['SEND_DATA', 'GET_PROFILES_ARRAY', 'GET_TRANSMITTED_PROFILES', 'GET_ACCEPTED_PROFILES']),
        handleCreateProfile() {
            if(!this.fullName) {
                this.$vToastify.info('Fill full name', 'Validation warning')
            } else {
                const data = {
                    method: 'createNewProfileContract',
                    params: [this.fullName]
                }
                this.SEND_DATA(data)
            }
        }
    },
    computed: {
        ...mapGetters(['PROFILES_ARRAY','TRANSMITTED_PROFILES', 'ACCEPTED_PROFILES'])
    }
}
</script>

<style scoped>
.home__container {
    width: 900px;
    margin: 0 auto;   
}
.home__title {
    margin-top: 30px;
    margin-bottom: 30px;
    font-size: 20px;
}
</style>
  